<template>
  <nav class="shopCar navbar-fixed-bottom" v-if="isbuyCar">
    <div v-if="isList" @click="$store.commit('isList',!isList)" class="alter">
    </div>
    <transition name="carList">
      <ul class="carList" v-if="isList">
        <p class="shopCarnav">
          <span class="pull-left">购物车</span>
          <span class="pull-right overI" @click="clearshop"><i class="iconfont">&#xe643;</i> 清空</span></p>
         <div class="liList">
           <li class="text" v-for="item in shopList" v-if="shopList[0]">
             <span class="pull-left foodname">{{item.name}}</span>
             <span class="price" v-if="item.price">{{item.price}}</span>
             <span class="price" v-else>{{item.specfoods[0].price}}</span>
             <span class="minus" @click="deleteS(item,item._id)" >-</span>
             <span class="cases">{{item.count}}</span>
             <span class="add" @click="addCar(item,item._id)">+</span>
           </li>
         </div>
      </ul>
    </transition>
   <div class="shopCarleft">
     <span class="shopBasket" :class="{shopB:isblue}">
       <i class="iconfont iconfont1" @click="isShowList">&#xe64f;</i>
     </span>
     <span class="price">¥ {{AllPrice}}.00</span>
     <p class="devprice">配送费约¥5</p>
   </div>
    <div class="shopCarRight">
      <div class="button" :class="{isgreen:isblue}">{{$store.state.zhang.sendUp}}</div>
    </div>
  </nav>
</template>

<script>
  export default {
    name: 'buyCart',
    data() {
      return {
        showMoveDot: [],
        AllPrice:0
      }
    },
    computed:{
      isbuyCar(){
        return this.$store.state.zhang.isbuyCar
      },
      isList(){
        return this.$store.state.zhang.isList
      },
      shopList(){
        return this.$store.state.zhang.shopCarList
      },
      isblue(){
        return this.$store.state.zhang.shopB
      }
    },
    created(){
      this.totalprice()
      if(localStorage.getItem(`${this.$store.state.zhang.id}购物车`)===null){
        this.$store.commit('updateshopList',null)
        this.$store.commit('updateids1',[])
      }else{
        this.$store.commit('updateshopList',JSON.parse(localStorage.getItem(`${this.$store.state.zhang.id}购物车`)))
        this.$store.commit('updateids1',JSON.parse(localStorage.getItem(`${this.$store.state.zhang.id}ids1`)))
      }
    },
    methods:{
      isShowList(){
        this.$store.commit('isList',!this.isList)
      },
      addCar(i,id){
        this.$store.commit('addshop',[i,id])
        this.totalprice()
      },
      deleteS(i,id){
        this.$store.commit('deleteS',[i,id])
        this.totalprice()
      },
      //计算总价格
      totalprice(){
        this.AllPrice=0
        this.shopList.forEach(item=>{
          if(item.price){
            this.AllPrice += item.count*item.price
          }else{
            this.AllPrice += item.count*item.specfoods[0].price
          }
        })
      },
      clearshop(){
        this.$store.commit('updateshopList',null)
        this.totalprice()
        this.$emit('changeminus',false)
      }
    },
    watch:{

    }
  }

</script>

<style lang="scss" scoped>
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.16rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    color: grey;
  }
  .iconfont1{
    font-size: 0.3rem !important;
    color: white !important;
  }
  .shopCar{
    width: 100%;
    height: 0.5rem;
    display: flex;
  }
  .shopCarleft{
    width: 70%;
    height: 100%;
    background-color: black;
    opacity: 0.9;
    position: relative;
    z-index: 1000;
  }
  .shopCarRight{
    width: 30%;
    height: 100%;
    position: relative;
    z-index: 1000;
  }
  .button{
    width: 100%;
    height: 100%;
    background-color:rgb(50,50,50);
    color: white;
    font-size: 0.16rem;
    line-height: 0.5rem;
  }
  .price,.devprice{
    color: white;
  }
  .price{
    font-size: 0.2rem;
  }
  .devprice{
    font-size: 0.1rem;
  }
  .shopBasket{
    background-color: rgba(10,10,10,1);
    border-radius: 50%;
    width: 0.6rem;
    height: 0.6rem;
    line-height: 0.5rem;
    border: 0.05rem solid rgba(0,0,0,0.9);
    display: inline-block;
    position: absolute;
    top:-0.2rem;
    left: 0.2rem;
  }
  .carList{
    width: 100%;
    max-height: 5rem;
    position: absolute;
    bottom: 0.3rem;
    z-index: 1000;
  }
  .carList-enter,.carList-leave-to{
    bottom:-5rem;
  }
  .carList-enter-active,.carList-leave-active{
    transition: all .3s ease;
  }
  .carList-leave,.carList-enter-to{
    bottom: 0.3rem;
  }
  .shopB{
    background-color: blue;
  }
  .isgreen{
    background-color: green !important;
  }
  .shopCarnav{
    width: 100%;
    height: 0.6rem;
    background-color: #e4e4e4;
    font-size: 0.16rem;
    line-height: 0.38rem;
  }
  .shopCarnav>span{
    padding: 0.1rem;
    display: inline-block;
  }
  .text{
    height: 0.8rem;
    width: 100%;
    background-color: white;
    font-size: 0.16rem;
    line-height: 0.8rem;
    display: flex;
    margin-top: -0.02rem;
    position: relative;
  }
  .text>span:nth-child(1){
    margin-left: 0.1rem;
  }
  .alter{
    width: 100%;
    height: 6.5rem;
    background-color: black;
    opacity: 0.5;
    position: absolute;
    top: -6.5rem;
    left: 0;
    z-index: 99;
  }
  .add{
    width: 0.2rem !important;
    height: 0.2rem !important;
    position: relative;
    display: inline-block;
    left: 1.1rem;
    top:0.3rem;
    background-color: blue;
    color: white;
    text-align: center !important;
    line-height: 0.16rem;
    border-radius: 50%;
    font-size: 0.2rem !important;
  }
  .foodname{
    word-break: keep-all;
    text-overflow: ellipsis;
    overflow: hidden;
    font-weight: 900;
    width: 50%;
    text-align: left;
    font-size: 0.2rem;
  }
  .price{
    width: 10%;
    color: red;
  }
  .liList{
    max-height: 4rem;
    overflow: auto;
    width: 100%;
    margin-bottom: 0.2rem;
  }
  .overI{
    overflow: hidden;
    height: 0.6rem;
  }
  .minus{
    width: 0.2rem !important;
    height: 0.2rem !important;
    position: absolute;
    left: 2.8rem;
    top:0.3rem;
    background-color: white;
    border: 0.01rem solid black;
    color: black;
    text-align: center !important;
    line-height: 0.16rem;
    border-radius: 50%;
    font-size: 0.2rem !important;
  }
  .cases{
    position: absolute;
    right:0.45rem;
    top:0rem;
    font-size: 0.2rem !important;
  }
</style>




