 //获取数据的统一调配文件，对接口进行统一管理
 const getService = ()=>{
   this.$http({
    method:'get',
    url:'https://elm.cangdu.org/shopping/restaurants?',
    data:{
      latitude: this.$store.state.zhang.latitude,
      longitude: this.$store.state.zhang.longitude,
      limit:20,
      order_by:this.$store.state.zhang.count1,
      'delivery_mode[]':this.$store.state.zhang.distribution,
      'support_ids[]':this.$store.state.zhang.support,
      'restaurant_category_ids[]':this.$store.state.zhang.restaurant
    }
  }).then(res=>{
    this.$store.state.zhang.shoplist=res.data
  })
 }
export default getService




