<template>
  <div class="wrap">
    <ul class="alterul" v-if="isalter">
      <li v-for="alter1 in alterArr">
        <p>
          <span>{{fooditem.name}}</span>
          <span class="pull-right deletealter"  @click="isalter=!isalter">×</span>
        </p>
        <p class="guige">{{alter1.name}}</p>
        <p class="size">
          <span :class="isblue===index?'sizenext':''" v-for="(complete,index) in fooditem.specfoods" :key="index" @click="isblue=index">{{complete.specs_name}}</span>
        </p>
        <p class="add1">
          <span class="price pull-left">￥{{fooditem.specfoods[0].price}}</span>
          <button class="btn btn-info pull-right" @click="addCar(fooditem.specfoods[isblue],fooditem.specfoods[isblue]._id,fooditem)">加入购物车</button>
        </p>
      </li>
    </ul>
    <div v-if="isalter" @click="isalter=!isalter" class="alter">
    </div>
 <div class="box">
   <ul class="left" ref="l_list">
     <li @click="change(index)" ref="l_item" :class="{'act':index === actli}"
          v-for="(item,index) in arr" :key="index">
       <p>
         {{item.name}}
       </p>
     </li>
   </ul>
   <section class="r_list" ref="r_list">
     <div class="new">
       <div v-for="(it,index) in arr" :key="index" ref="product">
         <p class="title">{{it.name}}</p>
         <ul>
           <li v-for="(item,index) in it.foods" :key="index" class="list ">

             <div class="list_item flex" :class="index===it.foods.length-1?'last':''">
               <p class="picture"><img :src="http+item.image_path" alt=""></p>
               <div class="show">
                 <p class="name">{{item.name}}</p>
                 <p class="des">{{item.description}}</p>
                 <p class="sale">{{item.tips}}</p>
                 <p class="price">￥{{item.specfoods[0].price}}</p>
                 <div class="rightF">
                   <transition name="minus">
                     <p class="minus" v-if="item.count" @click="deleteS(item,item._id)">-</p>
                     <p class="minus" v-else-if="minus" @click="con">-</p>
                   </transition>
                   <p class="cases" v-if="item.count">{{item.count}}</p>
                   <p class="cases" v-else-if="item.count">{{fooditem.count}}</p>
                   <p v-if="item.specfoods[0].specs[0]"  class="specification" @click="alter1(item.specifications,item)">选{{item.specfoods[0].specs[0].name}}</p>
                   <p v-else class="add" @click="addCar(item,item._id)">+</p>
                 </div>
               </div>
             </div>
           </li>
         </ul>
       </div>
     </div>
   </section>
 </div>
    <buy-cart ref="chlid" @changeminus="changeminus"></buy-cart>
  </div>
</template>
<script>
  import Better from 'better-scroll'
  import Vue from 'vue'
  import BuyCart from "../../../components/common/buyCart";
    export default {
        name: "foodDetail",
        components: {BuyCart},
        data(){
        return {
          arr:[],
          actli:0,
          http:'http://elm.cangdu.org/img/',
          set:[0],
          left:'',
          rgt:'',
          flag:true,
          scrollY: 0,
          alterArr:[],
          isalter:false,
          fooditem:[],
          isblue:0,
          minus:false,
          cases:1
        }
      },
       created(){
          this.$store.commit('isbuyCar', true)
          var that=this
         if(localStorage.getItem(`${that.$store.state.zhang.id}`)===null){
           Vue.axios.get('https://elm.cangdu.org/shopping/v2/menu?restaurant_id='+that.$store.state.zhang.id,null).then((response)=>{
             that.arr=response.data
             that.$store.commit('getTotal',that.arr)
             this.$nextTick(()=>{
               that.left = new Better(that.$refs.l_list,{click:true})
               that.rgt = new Better(that.$refs.r_list,{click: true, probeType: 3})
               that.$refs.product.forEach((el, index)=>{
                 that.set.push(el.offsetHeight+that.set[index])
               })
               that.rgt.on('scroll',(res)=>{
                 if(that.flag){
                   that.scrollY = Math.abs(res.y)
                 }
                 for(let i = 0; i < that.set.length; i++){
                   if(that.scrollY > that.set[i] && that.scrollY < that.set[i + 1]){
                     that.actli = i
                     if(i === that.$refs.l_item.length - 2){
                       that.left.scrollToElement(that.$refs.l_item[1], 100, 0, 0)
                     }
                     if(i===2){
                       that.left.scrollToElement(that.$refs.l_item[0], 100, 0, 0)
                     }
                   }
                 }
               })
             })
           })
         }else{
            this.arr =JSON.parse(localStorage.getItem(`${that.$store.state.zhang.id}`))
            that.$store.commit('getTotal',this.arr)
           this.$nextTick(()=>{
             that.left = new Better(that.$refs.l_list,{click:true})
             that.rgt = new Better(that.$refs.r_list,{click: true, probeType: 3})
             that.$refs.product.forEach((el, index)=>{
               that.set.push(el.offsetHeight+that.set[index])
             })
             that.rgt.on('scroll',(res)=>{
               if(that.flag){
                 that.scrollY = Math.abs(res.y)
               }
               for(let i = 0; i < that.set.length; i++){
                 if(that.scrollY > that.set[i] && that.scrollY < that.set[i + 1]){
                   that.actli = i
                   if(i === that.$refs.l_item.length - 2){
                     that.left.scrollToElement(that.$refs.l_item[1], 100, 0, 0)
                   }
                   if(i===2){
                     that.left.scrollToElement(that.$refs.l_item[0], 100, 0, 0)
                   }
                 }
               }
             })
           })
         }

       },
      methods:{
        con(){
          console.log('111')
        },
        changeminus(res){
          this.minus = res
        },
        change(index){
          this.actli=index;
          this.flag = false;
          this.rgt.scrollToElement(this.$refs.product[index], 100, 0, 0)
          setTimeout(()=>{
            this.flag = true
          },100)
        },
        //加入购物车
        addCar(i,id,c){
          this.minus = true
          this.$store.commit('addshop',[i,id])
          if(c){
            Vue.set(c,'count',c.count+1)
          }
          this.$refs.chlid.totalprice()
        },
        alter1(i,f){
          this.alterArr = i
          this.fooditem = f
          console.log('规格',this.fooditem)
          this.isalter = !this.isalter
            },
        //减数的函数
        deleteS(i,id){
          this.$store.commit('deleteS',[i,id])
          this.$refs.chlid.totalprice()
        }
      },
      beforeRouteLeave(to,from,next){
        localStorage.setItem(`${this.$store.state.zhang.id}`,JSON.stringify(this.arr))
        localStorage.setItem(`${this.$store.state.zhang.id}购物车`,JSON.stringify(this.$store.state.zhang.shopCarList))
        localStorage.setItem(`${this.$store.state.zhang.id}ids1`,JSON.stringify(this.$store.state.zhang.ids1))
        next()
      },
      computed:{
          shoplist(){
            return this.$store.state.zhang.shopCarList
          },
          allcount(){
            return this.$store.state.zhang.allcount
        }
      },
      watch:{

      }
    }
</script>

<style scoped>
  .specical{
    width: 30%;
    height: 0.3rem;
  }
  .specification{
    width: 0.5rem !important;
    background-color: #3190e8;
    color: white;
    font-size: 0.16rem;
    border-radius: 0.05rem;
    line-height: 0.2rem;
    text-align: center !important;
    float: right;
  }
  .price{
    color: red;
  }
  #p1{
    width: 100%;
    height: 0.3rem;
    font-size: 0.2rem;
    background-color: #3190e8;
  }
  .wrap{
    width: 200% !important;
    padding-top: 0.2rem;
  }
  .box{
    width: 100%;
    display: flex;
  }
  .left{
    width: 30%;
    overflow: auto;
    height: 4.5rem;
  }
  .left::-webkit-scrollbar{
    display: none;
  }
  .left>li>p{
    width: 100%;
    height: 0.3rem;
    font-size: 0.16rem;
    text-align: center;
    line-height: 0.3rem;
    border-bottom: 0.01rem solid gray;
    text-overflow: ellipsis;
    word-break: break-word;
    overflow: hidden;
  }
  .r_list{
    width: 70%;
    overflow: auto;
    height: 4.5rem;
  }
  .new>div,.new{
    width: 100%;
  }
  .title{
    width: 100%;
    height: 0.3rem;
    font-size: 0.2rem;
    text-align: left;
    background-color: #90B4FC;
    text-overflow: ellipsis;
    word-break: break-word;
    overflow: hidden;
  }
  ul,li{
    width: 100%;
  }
  .flex{
    width: 100%;
    display: flex;
    position: relative;
  }
  .picture{
    width: 20%;
    height: 20%;
  }
  .picture>img{
    width: 100%;
    height: 100%;
  }
  .show{
    width: 70%;
    margin-left: 0.1rem;
  }
  .show>p{
    width: 100%;
    height: 0.25rem;
    font-size: 0.12rem;
    margin-top: 0.05rem;
    text-align: left;
  }
  .show>p:nth-child(1){
    text-overflow: ellipsis;
    word-break: break-word;
    overflow: hidden;
    font-size: 0.2rem;
    font-weight: 900;
  }
.act{
  background-color: deepskyblue;
}
.last{
  border-bottom: none;
}
  .a1{
    width: 0.15rem;
  }
  .add{
    width: 0.2rem !important;
    height: 0.2rem !important;
    background-color: blue;
    color: white;
    text-align: center !important;
    line-height: 0.16rem;
    border-radius: 50%;
    font-size: 0.2rem !important;
    display: inline-block;
    float: right;
  }
  .alter{
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
  }
  .alterul{
     height: 1.5rem;
     width:2.5rem ;
     position: absolute;
     background-color: white;
     left: 15%;
     top: 40%;
     opacity: 1 !important;
     z-index: 100;
     font-size: 0.16rem;
  }
  .alterul>li>p{
    margin: 0.1rem 0 0.1rem 0.05rem;
  }
  .guige{
    text-align: left;
  }
  .size{
    text-align: left;
  }
  .size>span{
    display: inline-block;
    width: 0.5rem;
    border: 0.01rem solid black;
    text-align: center;
    height: 0.3rem;
    line-height: 0.3rem;
    border-radius: 0.1rem;
  }
  .size>.sizenext{
    color: blue;
    border-color: blue ;
  }
  .add1{
    line-height: 0.5rem;
  }
  .btn{
    margin-right: 0.2rem;
  }
  .deletealter{
    font-size: 0.3rem;
    position: absolute;
    right: 0.05rem;
    top:0
  }
  .minus{
    width: 0.2rem !important;
    height: 0.2rem !important;
    position: absolute;
    background-color: white;
    border: 0.01rem solid black;
    color: black;
    left: 0;
    text-align: center !important;
    line-height: 0.16rem;
    border-radius: 50%;
    font-size: 0.2rem !important;
    display: inline-block;
    top:0rem
   }
  .minus-enter,.minus-leave-to{
    left:2.2rem;
    opacity: 0;
  }
  .minus-enter-active,.minus-leave-active{
    transition: all 0.5s ease;
  }
  .minus-leave,.-minus-enter-to{
    left: 1.8rem;
    opacity: 1;
  }
  .cases{
    font-size: 0.2rem !important;
    text-align: center;
    display: inline-block;
    margin-left: 0.2rem;
    position: relative;
    top: -0.1rem;
    height: 0.2rem;
  }
  .rightF{
    float: right;
    position: relative;
    max-width: 0.9rem;
    min-width: 0.7rem;
    height: 0.3rem;
  }
</style>



